<template>
    <v-container fluid ma-0 pa-0 full-height>
        <v-card>
            <v-container fluid ma-0 pa-0 full-height>
                <h2 class="text-md-center mt-5">{{radios}} Tahunan</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs9>
                        <v-btn v-if="listKB && (radios=='Buku Kas' || radios=='Buku Bank')" depressed dark rounded style="text-transform: none !important;"
                            class="primary mx-2" :to="'/reportpembukuantahunan/'+listKB" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Report
                        </v-btn>
                        <v-btn v-if="radios=='Buku Jurnal'" depressed dark rounded style="text-transform: none !important;"
                            class="primary mx-2" :to="'/reportjurnaltahunan'" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Report {{radios}} Tahunan
                        </v-btn>

                        <v-col cols="2" v-if="radios=='Buku Kas'">
                            <v-autocomplete class="mt-1" dense single-line outlined :items="listkb"
                                item-text="JenisPerkiraan" item-value="KodeUnik" label="Pilih Report*" v-model="listKB">
                            </v-autocomplete>
                        </v-col>
                        <v-col cols="2" v-if="radios=='Buku Bank'">
                            <v-autocomplete class="mt-1" dense single-line outlined :items="listkb"
                                item-text="JenisPerkiraan" item-value="KodeUnik" label="Pilih Report*" v-model="listKB">
                            </v-autocomplete>
                        </v-col>


                        <v-row no-gutters class="d-flex mt-4" v-if="radios=='Buku Jurnal'">
                        <v-col cols="2" class="mr-4">
                            <v-menu ref="menu3" v-model="menu3" :close-on-content-click="false"
                                :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                <template v-slot:activator="{ on }">
                                    <v-text-field v-model="form3.startdate" label="Start Date" dense single-line
                                        outlined v-on="on"></v-text-field>
                                </template>
                                <v-date-picker v-model="form3.startdate" no-title scrollable>
                                    <v-spacer></v-spacer>
                                    <v-btn text color="primary" @click="menu3 = false">Cancel</v-btn>
                                    <v-btn text color="primary" @click="$refs.menu3.save(date)">OK</v-btn>
                                </v-date-picker>
                            </v-menu>
                        </v-col>
                        <v-col cols="2" class="mr-4">
                            <v-menu ref="menu4" v-model="menu4" :close-on-content-click="false"
                                :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                <template v-slot:activator="{ on }">
                                    <v-text-field v-model="form3.enddate" label="End Date" dense single-line outlined
                                        v-on="on"></v-text-field>
                                </template>
                                <v-date-picker v-model="form3.enddate" no-title scrollable>
                                    <v-spacer></v-spacer>
                                    <v-btn text color="primary" @click="menu4 = false">Cancel</v-btn>
                                    <v-btn text color="primary" @click="$refs.menu4.save(date), trigger=true">OK</v-btn>
                                </v-date-picker>
                            </v-menu>
                        </v-col>
                        <v-col cols="1">
                            <v-btn depressed dark rounded style="text-transform: none !important;" class="mr-4"
                                @click="getData(), trigger=false">
                                <v-icon size="18">mdi-magnify</v-icon>
                            </v-btn>
                        </v-col>
                        </v-row>

                        
                        
                        <v-row class="d-flex" no-gutters v-if="radios=='Buku Besar'">
                            <v-col cols="5" class="d-flex" v-if="radios=='Buku Besar'">
                                <v-text-field v-model="findKodeper" dense single-line outlined rounded class="mr-4"
                                    label="Cari Data . . . . ."></v-text-field>
                                <v-btn depressed dark rounded style="text-transform: none !important;" @click="getBuku()" class="mr-4">
                                    <v-icon size="18">mdi-magnify</v-icon>
                                </v-btn>
                                <v-btn v-if="statusButton && radios=='Buku Besar'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                                    :to="'/reportbukubesartahunan/'+findKodeper+'/'+radios" link>
                                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                                    Report Data Tahunan
                                </v-btn>
                            </v-col>
                            <v-col cols="7">
                            </v-col>
                            <v-col cols="2" class="mt-2">
                                <h4>Nomor Perkiraan</h4>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field v-model="listper.KodeUnik" dense single-line outlined readonly></v-text-field>
                            </v-col>
                            <v-col cols="2" class="ml-10 mt-2">
                                <h4>Nama Perkiraan</h4>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field v-model="listper.JenisPerkiraan" dense single-line outlined readonly class="mr-4"></v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                        </v-row>

                        <v-row no-gutters class="d-flex" v-if="radios=='Buku Besar Daerah'">
                            <v-col cols="5" class="d-flex" v-if="radios=='Buku Besar Daerah'">
                                <v-text-field v-model="findKodeper" dense single-line outlined rounded class="mr-4"
                                    label="Cari Data . . . . ."></v-text-field>
                                <v-btn depressed dark rounded style="text-transform: none !important;" @click="getBuku()" class="mr-4">
                                    <v-icon size="18">mdi-magnify</v-icon>
                                </v-btn>

                                <v-btn v-if="statusButton && radios=='Buku Besar Daerah'" depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                                    :to="'/reportbukubesartahunan/'+findKodeper+'/'+radios" link>
                                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                                    Report Data
                                </v-btn>

                            </v-col>
                            <v-col cols="7">
                            </v-col>
                            <v-col cols="2" class="mt-2">
                                <h4>Nomor Perkiraan</h4>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field v-model="listper.KodeUnik" dense single-line outlined readonly></v-text-field>
                            </v-col>
                            <v-col cols="2" class="ml-10 mt-2">
                                <h4>Nama Perkiraan</h4>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field v-model="listper.JenisPerkiraan" dense single-line outlined readonly class="mr-4"></v-text-field>
                            </v-col>
                            <v-col cols="3"></v-col>
                        </v-row>

                        
                        <v-radio-group v-model="radios" @change="getData(), setHeaders()" row>
                            <v-radio label="Buku Kas" value="Buku Kas"></v-radio>
                            <v-radio label="Buku Bank" value="Buku Bank"></v-radio>
                            <v-radio label="Buku Jurnal" value="Buku Jurnal"></v-radio>
                            <v-radio label="Buku Besar" value="Buku Besar"></v-radio>
                            <v-radio label="Buku Besar Daerah" value="Buku Besar Daerah"></v-radio>
                        </v-radio-group>

                        

                              <!-- <v-col class="d-flex" cols="12" sm="6">
                                  <v-autocomplete :items="saldos" item-label="Pilih Saldo" 
                                  item-text="NamaSaldo" item-value="JumlahSaldo"
                                  v-model="form.saldo" dense solo></v-autocomplete>
                              </v-col>
                            <v-col cols="12" sm="6">
                                <v-text-field v-model="form.saldo" dense solo readonly></v-text-field>
                            </v-col> -->
                    </v-flex>
                    <v-flex xs3 class="text-right">
                        <v-col cols="12">
                            <v-text-field v-model="keyword" dense single-line outlined append-icon="mdi-search" label="Cari Data" hide-details>
                            </v-text-field>
                        </v-col>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="bukus" :search="keyword">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.id">

                                <!-- <td>{{ SplitInto3Part(item["kodedebet"], '###-##-####') }}</td>
                                <td>{{ SplitInto3Part(item["kodekredit"], '###-##-####') }}</td> -->
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["tanggal"] }} </td>
                                <td class="title" v-if="item.nobukti=='null'"></td>
                                <td class="title" v-else>{{ item["nobukti"]}}</td>
                                <td class="title">{{ item["keterangan"] }}</td>

                                <td class="title" v-if="radios=='Buku Kas'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title" v-if="radios=='Buku Kas'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>
                               
                                <td class="title" v-if="radios=='Buku Bank'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title" v-if="radios=='Buku Bank'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>

                                <td class="title" v-if="radios=='Buku Jurnal'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>
                                <td class="title" v-if="radios=='Buku Jurnal'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>

                                <td class="title" v-if="radios=='Buku Besar' || radios=='Buku Besar Daerah'">{{ item["kode"].slice(0,3) + '-' + item["kode"].slice(3,5) + '-' + item["kode"].slice(5,9) }}</td>
                                <!-- <td class="title" v-if="radios!='Buku Jurnal'">{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td>
                                <td class="title" v-else>{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title"  v-if="radios!='Buku Jurnal'">{{ item["kodekredit"].slice(0,3) + '-' + item["kodekredit"].slice(3,5) + '-' + item["kodekredit"].slice(5,9) }}</td>
                                <td class="title" v-else>{{ item["kodedebet"].slice(0,3) + '-' + item["kodedebet"].slice(3,5) + '-' + item["kodedebet"].slice(5,9) }}</td> -->
                                <td class="title" v-if="radios=='Buku Kas'">Rp {{ item["kredit"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Kas'">Rp {{ item["debet"] | currency}}</td>
                                
                                <td class="title" v-if="radios=='Buku Bank'">Rp {{ item["kredit"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Bank'">Rp {{ item["debet"] | currency}}</td>

                                <td class="title" v-if="radios=='Buku Jurnal'">Rp {{ item["debet"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Jurnal'">Rp {{ item["kredit"] | currency}}</td>
                                
                                <td class="title" v-if="radios=='Buku Besar' || radios=='Buku Besar Daerah'">Rp {{ item["debet"] | currency}}</td>
                                <td class="title" v-if="radios=='Buku Besar' || radios=='Buku Besar Daerah'">Rp {{ item["kredit"] | currency}}</td>
                                <td class="title">
                                    <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click="deleteData(item)">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>

        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>

export default {
    data() {
        return {
            radios:'Buku Kas',
            date: new Date().toISOString().substr(0, 10),
            menu: false,
            menu3: false,
            menu4: false,
            trigger: false,
            trigger_insert: false,
            bukus: [],
            listperkiraans: [],
            listall: [],
            // listbank: [],
            // listlain: [],
            // listkas: [],
            listper: [],
            listkb: [],
            listdaerah: [],
            listKB: '',
            statusUpdate: true,
            dialog: false,
            dialog2: false,
            dialog3: false,
            keyword: '',
            headers: [{
                    text: 'No',
                    value: 'no',
                    class: 'title'
                },
                {
                    text: 'Tanggal',
                    value: 'tanggal',
                    class: 'title'
                },
                {
                    text: 'No Bukti',
                    value: 'nobukti',
                    class: 'title'
                },
                {
                    text: 'Keterangan',
                    value: 'keterangan',
                    class: 'title'
                },
                {
                    text: 'Kode Perkiraan',
                    value: 'kodedebet',
                    class: 'title'
                },
                {
                    text: 'Kode Lawan',
                    value: 'kodekredit',
                    class: 'title'
                },
                {
                    text: 'Debet',
                    value: 'debet',
                    class: 'title'
                },
                {
                    text: 'Kredit',
                    value: 'kredit',
                    class: 'title'
                },
                {
                    text: 'Aksi',
                    class: 'title',
                    value: null
                },
            ],
            snackbar: false,
            color: null,
            text: '',
            load: false,
            findKodeper: '',
            edit: {
                tanggal: '',
                nobukti: '',
                keterangan: '',
                kodekredit: '',
                kodedebet: '',
                debet: 0,
                kredit: 0
            },
            form3: {
                startdate: '',
                enddate: '',
            },

            form2: {
                Kas: '',
            },
            form: [{
                tanggal: '',
                nobukti: '',
                keterangan: '',
                Kas: '',
                kodeLawan: '',
                daerah: '00',
                debet: 0,
                kredit: 0,
                menu: false,
            }],
            listper: '',
            buku: new FormData,
            typeInput: 'new',
            errors: '',
            updatedId: '',
            statusButton: false,
        }
    },
    
    methods: {
        // getSaldo() {
        // var uri = this.$apiUrl + 'saldo'
        // this.$http.get(uri).then(response => {
        //     this.saldos = response.data
        //     })
        // },
        setHeaders(){
            if (this.radios=="Buku Bank" || this.radios=="Buku Kas" || this.radios=="Buku Jurnal") {
                this.headers = [{
                        text: 'No',
                        value: 'no',
                        class: 'title'
                    },
                    {
                        text: 'Tanggal',
                        value: 'tanggal',
                        class: 'title'
                    },
                    {
                        text: 'No Bukti',
                        value: 'nobukti',
                        class: 'title'
                    },
                    {
                        text: 'Keterangan',
                        value: 'keterangan',
                        class: 'title'
                    },
                    {
                        text: 'Kode Perkiraan',
                        value: 'kodedebet',
                        class: 'title'
                    },
                    {
                        text: 'Kode Lawan',
                        value: 'kodekredit',
                        class: 'title'
                    },
                    {
                        text: 'Debet',
                        value: 'debet',
                        class: 'title'
                    },
                    {
                        text: 'Kredit',
                        value: 'kredit',
                        class: 'title'
                    },
                    {
                        text: 'Aksi',
                        class: 'title',
                        value: null
                    },
                ]
            }
            else if (this.radios=="Buku Besar" || this.radios=="Buku Besar Daerah") {
                this.headers = [{
                        text: 'No',
                        value: 'no',
                        class: 'title'
                    },
                    {
                        text: 'Tanggal',
                        value: 'tanggal',
                        class: 'title'
                    },
                    {
                        text: 'No Bukti',
                        value: 'nobukti',
                        class: 'title'
                    },
                    {
                        text: 'Keterangan',
                        value: 'keterangan',
                        class: 'title'
                    },
                    {
                        text: 'Kode Lawan',
                        value: 'kodekredit',
                        class: 'title'
                    },
                    {
                        text: 'Debet',
                        value: 'debet',
                        class: 'title'
                    },
                    {
                        text: 'Kredit',
                        value: 'kredit',
                        class: 'title'
                    },
                    {
                        text: 'Aksi',
                        class: 'title',
                        value: null
                    },
                ]
            }
        },
        postingBtn(){
            this.dialog3 = true
        },

        tambahBtn() {
            this.dialog = true;
            this.getList();
        },
        
        getBuku() {
            if (this.radios == "Buku Besar") {
                var uri = this.$apiUrl + 'bukutahunan/buku_besar/' + this.findKodeper
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                    this.statusButton = true
                })
            } else if (this.radios == "Buku Besar Daerah") {
                var uri = this.$apiUrl + 'bukutahunan/buku_besar_daerah/' + this.findKodeper
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                    this.statusButton = true
                })
            }

            var uri2 = this.$apiUrl + 'listperkiraan/' + this.findKodeper
            this.$http.get(uri2 ).then(response => {
                this.listper = response.data[0]
            })

        },

        getBukuKas() {
            var uri = this.$apiUrl + 'bukutahunan/buku_kas'
            this.$http.get(uri).then(response => {
                this.bukus = response.data
            })
        },

        getData() {
            this.bukus = []
            if (this.radios == 'Buku Kas') {
                var uri = this.$apiUrl + 'bukutahunan/buku_kas'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                })
            } else if (this.radios == 'Buku Bank') {
                var uri = this.$apiUrl + 'bukutahunan/buku_bank'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data
                })
            } else if (this.radios == 'Buku Jurnal') {
                var uri = this.$apiUrl + 'bukutahunan'
                this.$http.get(uri).then(response => {
                    this.bukus = response.data

                })
                if (this.trigger == true) {
                    var uri = this.$apiUrl + 'bukutahunan/' + this.form3.startdate + '/' + this.form3.enddate
                    this.$http.get(uri).then(response => {
                        this.bukus = response.data
                        this.resetForm3();
                    })
                }
            } 
        },
        getListKB(){
            var uri = this.$apiUrl + 'listkasbank'
            this.$http.get(uri).then(response =>{
                this.listkb = response.data
            })
        },
        Activity(key) {
            // untuk memposting aktivitas user yang login
            var activity = new FormData
            activity.append('activity', key)
            var uri = this.$apiUrl + 'activity'
            this.$http.post(uri, activity).then(response => {}).catch(error => {
                this.errors = error
            })
        },
        close() {
            this.dialog = false
            this.dialog2 = false
            this.resetForm()
        },
        resetForm() {
            this.form = {
                    tanggal: '',
                    nobukti: '',
                    keterangan: '',
                    Kas: '',
                    kodeLawan: '',
                    jumlah: 0,
                },
                this.form = [{
                    tanggal: '',
                    nobukti: '',
                    keterangan: '',
                    Kas: '',
                    kodeLawan: '',
                    daerah: '00',
                    debet: 0,
                    kredit: 0,
                    menu: false,
                }],
                this.statusUpdate = true
        },
        resetForm2() {
            this.edit = {
                    tanggal: '',
                    nobukti: '',
                    keterangan: '',
                    kodedebet: '',
                    kodekredit: '',
                    jumlah: 0,
                },

                this.statusUpdate = true
        },
        resetForm3(){
            this.form3 = {
                startdate: '',
                enddate: '',
            }
            this.trigger=false
        }

    },
    mounted(){
        this.getBukuKas();
        this.getListKB();
    },
}
</script>